.nav-mobile {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: flex-end;
  padding: 2rem 2rem;
}
@media screen and (min-width: 64rem) {
  .nav-mobile {
    display: none;
  }
}
.nav-mobile .ham {
  color: var(--warna-cream);
}
.nav-mobile .nav-ham {
  color: var(--warna-biru);
  font-size: 8vw;
  top: 0;
  right: -100%;
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: var(--warna-cream);
}
.nav-mobile .nav-ham .link {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.nav-mobile .nav-ham .link .about {
  margin-bottom: 2rem;
  overflow: hidden;
}
.nav-mobile .nav-ham .link .contact {
  overflow: hidden;
}
.nav-mobile .nav-ham .close {
  padding: 2rem 2rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  position: absolute;
}

.nav-home-page {
  font-size: 1vw;
  width: 100vw;
  position: absolute;
  color: var(--warna-cream);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 64rem) {
  .nav-home-page {
    display: none;
  }
}
.nav-home-page .nav-kiri {
  overflow: hidden;
  padding: 3rem 0 3rem 3rem;
  margin-right: 1rem;
  width: 100%;
}
.nav-home-page .nav-kiri .nav-kiri-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.nav-home-page .nav-kiri .nav-kiri-container #about-container {
  z-index: 1;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  border-radius: 40px;
  padding: 0.5rem 2rem;
  border: 1px solid var(--warna-biru);
  transition: border 0.5s ease;
}
.nav-home-page .nav-kiri .nav-kiri-container #about-container:hover {
  border: 1px solid var(--warna-cream);
}
.nav-home-page .nav-kiri .nav-kiri-container #about-container .about {
  display: flex;
  flex-direction: row;
}
.nav-home-page .nav-kiri .nav-kiri-container #about2-container {
  z-index: 0;
  position: absolute;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding: 0.5rem 2rem;
}
.nav-home-page .nav-kiri .nav-kiri-container #about2-container .about2 {
  display: flex;
  flex-direction: row;
}
.nav-home-page .nav-kanan {
  overflow: hidden;
  padding: 3rem 3rem 3rem 0;
  margin-left: 1rem;
  width: 100%;
  color: var(--warna-biru);
}
.nav-home-page .nav-kanan .nav-kanan-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.nav-home-page .nav-kanan .nav-kanan-container #contact-container {
  z-index: 1;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border-radius: 40px;
  padding: 0.5rem 2rem;
  border: 1px solid var(--warna-cream);
  transition: border 0.5s ease;
}
.nav-home-page .nav-kanan .nav-kanan-container #contact-container:hover {
  border: 1px solid var(--warna-biru);
}
.nav-home-page .nav-kanan .nav-kanan-container #contact-container .contact {
  display: flex;
  flex-direction: row;
}
.nav-home-page .nav-kanan .nav-kanan-container #contact2-container {
  right: 3rem;
  z-index: 0;
  position: absolute;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  padding: 0.5rem 2rem;
}
.nav-home-page .nav-kanan .nav-kanan-container #contact2-container .contact2 {
  display: flex;
  flex-direction: row;
}